<template>
  <div>
    <div id="searchInfo">
      <!---->
      <select class="selectWM" v-model="statechangeKey.schoolYearType">
        <option value="0"></option>
        <option v-for="yearType in yearTypes" :value="yearType">{{yearType}}</option>
      </select>
      <span><input type="text" id="stdID" class="inputWM" placeholder="" v-model="statechangeKey.studentId"></span>
      <span><input type="text" id="stdName" class="inputWM" placeholder="" v-model="statechangeKey.studentName"></span>
      <span><button id="searchFor" class="am-btn am-btn-success am-radius buttonWM" @click="searchChangeInfo()"></button></span>
    </div>
    <div id="stdInfoTable" style="padding: 0.6rem 5rem;background-color: #f3f3f3">
      <!---->
      <table id="stdInfoTableSy" class="normalTable" style="table-layout: fixed;">
        <thead>
        <tr>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="statechangeinfoStr in statechangeinfoStrList">
          <td v-text="statechangeinfoStr.schoolYearType + ''"></td>
          <td v-text="statechangeinfoStr.specialityName"></td>
          <td v-text="statechangeinfoStr.className"></td>
          <td v-text="statechangeinfoStr.studentId"></td>
          <td v-text="statechangeinfoStr.studentName"></td>
          <td v-text="statechangeinfoStr.changeType"></td>
          <td v-text="statechangeinfoStr.changeReason"></td>
          <td v-text="statechangeinfoStr.changeDate"></td>
        </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
    export default {
        name: '',
        data () {
            return {
              yearTypes:[
                '3',
                '5'
              ],
              statechangeKey:{
                schoolYearType:'0',
                studentId:'',
                studentName:''
              },
              statechangeinfoStrList:[
                  {schoolYearType:'3',specialityName:'',className:'3',studentId:'1530310503',studentName:'',changeType:'',changeReason:'',changeDate:'2016.03.01'},
                  {schoolYearType:'5',specialityName:'',className:'4',studentId:'1530310501',studentName:'',changeType:'',changeReason:'',changeDate:'2016.03.02'},
                  {schoolYearType:'3',specialityName:'',className:'1',studentId:'1530310502',studentName:'',changeType:'',changeReason:'',changeDate:'2015.11.23'}
                ]
            }
        },
      beforeMount:function() {
        this.$http.post('./stateManage/getAllStateChangeInfo',{},{
          "Content-Type":"application/json"
        }).then(function (response) {
          console.log(response);
          this.statechangeinfoStrList = response.body.statechangeinfoStrList;
        },function(error){
          console.log("error");
        });
      },
      methods:{
        searchChangeInfo: function(){
          this.$http.post('./stateManage/findStateChangeInfo',{
            "statechangeKey":this.statechangeKey
          },{
            "Content-Type":"application/json"
          }).then(function (response) {
            console.log(response);
            this.statechangeinfoStrList = response.body.statechangeinfoStrList;
          },function(error){
            console.log("error");
          });
        }
      }
    }
</script>

<style scoped>
    html {
        font-size: 100%;
    }
    #searchInfo{
       margin: 0.6rem 5rem;
       background-color: white;
     }
    .selectWM{
      width: 8rem;
      margin: 0 0.7rem;
    }
    .inputWM{
      width: 8rem;
      margin: 0 0.7rem;
    }
    .buttonWM{
      width: 5.6rem;
      margin: 0 0.7rem;
    }
    @media screen and (max-width: 1023px) {
        html {
            font-size: 56%;
        }
    }
</style>



