<template>
  <div>
    <div id="noSupervisorDiv" style="display: inline">
      <div id="tchDropdown" style="height: 5rem;">
        <!---->
        <span><input type="text" id="teacherName" class="inputWM" placeholder="" v-model="noSupervisorinfoKey.teacherName"></span>
        <span><input type="text" id="courseName" class="inputWM" placeholder="" v-model="noSupervisorinfoKey.courseName"></span>
        <span><button id="searchFor" class="am-btn am-btn-success am-radius buttonWM" @click="checkNoSupervisorInfoClick()"></button></span>
        <span><button id="leadOut" class="am-btn am-btn-success am-radius buttonWM"></button></span>
      </div>
      <div id="noSupervisorTable" style="padding: 0.6rem 5rem;background-color: #f3f3f3">
        <span><p>*</p></span>
        <!---->
        <table id="noSupervisorTableSy" class="normalTable" style="table-layout: fixed;">
          <thead>
          <tr>
            <th width="15%"></th>
            <th width="25%"></th>
            <th width="20%"></th>
            <th width="25%"></th>
            <th width="15%"></th>
          </tr>
          </thead>
          <tbody>
          <tr v-for=" courseInfo in courseInfoList" v-if="courseInfo.setted === '0'">
            <td @click="setSupervisorClick(courseInfo.courseId,courseInfo.courseName)"  class="setSupervisor"><u></u></td>
            <td v-text="courseInfo.className"></td>
            <td v-text="courseInfo.courseId"></td>
            <td v-text="courseInfo.courseName"></td>
            <td v-text="courseInfo.teacherName"></td>
          </tr>
          </tbody>
        </table>
      </div>
    </div>
    <div id="supervisorDiv" style="display: none">
      <div id="setSupervisorDropdown" style="height: 5rem;">
        <span><p>:    {{setCourseName}}</p></span>
        <div>
          <span id="setSupSpan">:</span>
          <span><input type="text" id="supervisorName" class="inputWM" placeholder="" v-model="supervisorinfoKey.supervisorName"></span>
          <!---->
          <select class="selectWM" v-model="supervisorinfoKey.time">
            <option value="0"></option>
            <option v-for="time in times" :value="time">{{time}}</option>
          </select>
          <span><button id="save" class="am-btn am-btn-success am-radius buttonWM" @click="saveSupervisorInfoClick()"></button></span>
          <span><button id="cancel" class="am-btn am-btn-success am-radius buttonWM" @click="restoreSupervisorInfoClick()"></button></span>
        </div>
      </div>
      <div style="padding: 0.6rem 5rem;background-color: #f3f3f3">
        <div id="supervisorTable" style="background-color: white">
          <span><p>*</p></span>
          <table id="supervisorTableSy" class="operationTable" style="table-layout: fixed;">
            <!---->
            <thead>
            <tr>
              <th width="12%"></th>
              <th width="12%"></th>
              <th width="12%"></th>
              <th width="16%"></th>
              <th width="24%"></th>
              <th width="12%"></th>
              <th width="12%"></th>
            </tr>
            </thead>
            <tbody>
            <tr v-for=" courseInfo in courseInfoList" v-if="courseInfo.setted === '1'">
              <td v-if="courseInfo.status === '1'"></td>
              <td v-if="courseInfo.status === '0'"><u></u></td>
              <td v-text="courseInfo.supervisorName"></td>
              <td v-text="courseInfo.className"></td>
              <td v-text="courseInfo.courseId"></td>
              <td v-text="courseInfo.courseName"></td>
              <td v-text="courseInfo.teacherName"></td>
              <td><button class="am-btn am-btn-success am-radius"></button></td>
            </tr>
            </tbody>
          </table>
          <div id="buttonDiv">
            <span><button id="goBack" class="bottomButton am-btn am-btn-success am-radius" @click="goBackClick()"></button></span>
          </div>
        </div>
      </div>
    </div>
  </div>

</template>

<script>
    export default {
        name: 'noSupervisorTable',
        data () {
            return {
              noSupervisorinfoKey:{
                teacherName:'',
                courseName:''
              },
              supervisorinfoKey:{
                supervisorName:'',
                time:'0'
              },
              setCourseName:'',
              times:[
                '1-5',
                '6-10',
                '7-15',
                '16-20'
              ],
                courseInfoList:[
                  {setted:'0',status:'1',supervisorName:'',className:' 2015 1',courseName:'',courseId:'10300',teacherName:''},
                  {setted:'0',status:'0',supervisorName:'',className:' 2015 2',courseName:'',courseId:'10300',teacherName:''},
                  {setted:'1',status:'1',supervisorName:'',className:' 2015 1',courseName:'',courseId:'10301',teacherName:''},
                  {setted:'1',status:'0',supervisorName:'',className:' 2015 2',courseName:'',courseId:'10301',teacherName:''}
                ]
            }
        },
      beforeMount:function() {
        this.$http.post('../supervisorManageJson',{},{
          "Content-Type":"application/json"
        }).then(function (response) {
          console.log(response);
          this.courseInfoList = response.body.courseInfoList;
        },function(error){
          console.log("error");
        });
      },
      methods:{
        checkNoSupervisorInfoClick: function(){
          this.$http.post('../checkNoSupervisorInfoJson',{
            "noSupervisorinfoKey":this.noSupervisorinfoKey
          },{
            "Content-Type":"application/json"
          }).then(function (response) {
            console.log(response);
            this.courseInfoList = response.body.courseInfoList;
          },function(error){
            console.log("error");
          });
        },
        setSupervisorClick:function(courseId,courseName){
          var noSupervisorDiv = document.getElementById("noSupervisorDiv");
          var supervisorDiv = document.getElementById("supervisorDiv");
          this.setCourseName = courseName;
          this.$http.post('../setSupervisorJson',{
            "courseId":courseId
          },{
            "Content-Type":"application/json"
          }).then(function (response) {
            console.log(response);
            this.courseInfoList = response.body.setSupervisor.courseInfoList;
            this.times = response.body.setSupervisor.times;
          },function(error){
            console.log("error");
          });
          noSupervisorDiv.style.display = "none";
          supervisorDiv.style.display = "inline";
        },
        saveSupervisorInfoClick:function(){
          this.$http.post('../saveSupervisorInfoJson',{
            "supervisorinfoKey":this.supervisorinfoKey
          },{
            "Content-Type":"application/json"
          }).then(function (response) {
            console.log(response);
            this.courseInfoList = response.body.courseInfoList;
          },function(error){
            console.log("error");
          });
        },
        restoreSupervisorInfoClick:function(){
          this.supervisorinfoKey.supervisorName = '';
          this.supervisorinfoKey.time = '0';
        },
        goBackClick:function(){
          noSupervisorDiv.style.display = "inline";
          supervisorDiv.style.display = "none";
        }
      }
    }
</script>

<style scoped>
    html {
        font-size: 62.5%;
    }
    #tchDropdown,#setSupervisorDropdown{
      margin: 0.6rem 5rem;
      background-color: white;
    }
    .selectWM{
      width: 8rem;
      margin: 0 0.7rem;
    }
    .inputWM{
      width: 8rem;
      margin: 0 0.7rem;
    }
    .buttonWM{
      width: 5.6rem;
      margin: 0 0.7rem;
    }
    p{
      color: grey;
    }
    .setSupervisor{
      color: #158064;
      cursor: pointer;
    }
    .bottomButton{
      margin-top: 1rem;
      margin-right: 1.4rem;
    }
    #buttonDiv{
      text-align: center;
    }
    @media screen and (max-width: 1023px) {
        html {
            font-size: 56%;
        }
    }
</style>



