<template>
  <div>
    <div id="searchInfo">
      <span><input type="text" id="groupID" class="inputWM" placeholder="" v-model="teacherinfoKey.groupName"></span>
      <span><input type="text" id="tchID" class="inputWM" placeholder="" v-model="teacherinfoKey.teacherName"></span>
      <span><button id="searchFor" class="am-btn am-btn-success am-radius buttonWM" @click="checkTeachingPlanInfoClick()"></button></span>
    </div>
    <div style="padding: 0.6rem 5rem;background-color: #f3f3f3">
      <div id="courseGroupTableDiv" style="background-color: white">
        <!---->
        <table id="courseGroupTable" class="operationTable" style="table-layout: fixed;">
          <thead>
          <tr>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
          </thead>
          <tbody>
          <tr v-for=" courseGroupInfo in  courseGroupInfos">
            <td v-text="courseGroupInfo.groupId"></td>
            <td v-text="courseGroupInfo.groupName"></td>
            <td v-text="courseGroupInfo.teacherId"></td>
            <td v-text="courseGroupInfo.teacherName"></td>
            <td v-text="courseGroupInfo.headman"></td>
            <td><button id="download" class="am-btn am-btn-success am-radius"></button></td>
            <!---->
            <td>
              <img id="adopt" src="./images/save.png" @click="adoptTeachingPlanInfoClick(courseGroupInfo.teacherId)">
              <img id="notAdopt" src="./images/restore.png" @click="notAdoptTeachingPlanInfoClick(courseGroupInfo.teacherId)">
            </td>
          </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>

<script>
    export default {
        name: '',
        data () {
            return {
              teacherinfoKey:{
                groupName:'',
                teacherName:''
              },
                courseGroupInfos:[
                  {groupName:'',groupId:'2017001',teacherName:'',teacherId:'111', headman:''},
                  {groupName:'',groupId:'2017002',teacherName:'',teacherId:'222',headman:''},
                  {groupName:'',groupId:'2017003',teacherName:'',teacherId:'333',headman:''}
                ]
            }
        },
      beforeMount:function() {
        this.$http.post('../teachingPlanJson',{},{
          "Content-Type":"application/json"
        }).then(function (response) {
          console.log(response);
          this.courseGroupInfos = response.body.courseGroupInfos;
        },function(error){
          console.log("error");
        });
      },
      methods:{
        checkTeachingPlanInfoClick: function(){
          this.$http.post('../checkTeachingPlanInfoJson',{
            "teacherinfoKey":teacherinfoKey
          },{
            "Content-Type":"application/json"
          }).then(function (response) {
            console.log(response);
            this.courseGroupInfos = response.body.courseGroupInfos;
          },function(error){
            console.log("error");
          });
        },
        adoptTeachingPlanInfoClick: function(teacherId){
          this.$http.post('../adoptTeachingPlanInfoJson',{
            "teacherId":teacherId
          },{
            "Content-Type":"application/json"
          }).then(function (response) {
            console.log(response);
          },function(error){
            console.log("error");
          });
        },
        notAdoptTeachingPlanInfoClick: function(teacherId){
          this.$http.post('../notAdoptTeachingPlanInfoJson',{
            "teacherId":teacherId
          },{
            "Content-Type":"application/json"
          }).then(function (response) {
            console.log(response);
          },function(error){
            console.log("error");
          });
        }
      }
    }
</script>

<style scoped>
    html {
        font-size: 100%;
    }
    #searchInfo{
      margin: 0.6rem 5rem;
      background-color: white;
    }
    .selectWM{
      width: 8rem;
      margin: 0 0.7rem;
    }
    .inputWM{
      width: 8rem;
      margin: 0 0.7rem;
    }
    .buttonWM{
      width: 5.6rem;
      margin: 0 0.7rem;
    }
    img{
      width: 2rem;
      height: 2rem;
      border: thin solid white;
    }
    img:hover{
      cursor: pointer;
      border: thin solid grey;
    }
    @media screen and (max-width: 1023px) {
        html {
            font-size: 56%;
        }
    }
</style>



